
.home-container  {
    min-height: 90vh;
    display: flex;
    margin-bottom: 1px solid (var(--quinta-cor));
}
.home-container div {
    width: 50%;
    padding: 0.5rem;
}
.home-desc {
    width: 100%;
    min-width: 400px;
    margin: 2rem auto;
}
.home-desc h1, 
h2 {
    text-transform: uppercase;
    text-align: start;
    font-weight: bolder;
}
.home-desc h1 {
    font-size: 35px;
}
.home-desc h2 {
    font-size: 20px;
    color: var(--quinta-cor);
}
.home-desc p {
    text-align: justify;
    margin: 2rem 0;
    color: black;
}
.home-desc button {
    width: 150px;
    height: 40px;
    float: left;
    text-transform: uppercase;
    border-radius: 25%;
    border: transparent;
    background-color: var(--quarta-cor);
    cursor: pointer;
}
.home-desc button a {
    text-decoration: none;
    color: white;
}
.home-desc button:hover {
    box-shadow: 1px 1px 5px black;
    transition: 0.5s;
}
/*Imagem Home*/
.imagem-home img {
    max-width: 500px;
}

@media(max-width:768px) {
    .home-container {
        flex-direction: column;
    }
    .home-container div {
        width: 90%;
        margin: 0 auto;
    }
    .home-desc {
        min-width: 200px;
    }
    .imagem-home img {
        max-width: 300px;
        text-align: center;
    }
}