/*Container mobile aberto*/
.container-mobile-aberto {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: white;
    top: 0;
    right: 0;
    z-index: 1;
    overflow: hidden;
}
.container-mobile-aberto nav ul {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 15vh auto;
    width: 50%;
}
.container-mobile-aberto nav ul li {
    list-style: none;
}
.container-mobile-aberto nav ul li a {
    text-decoration: none;
    color: black;
    font-size: 18px;
}