
header {
    width: 80%;
    margin: 0 auto;
    height: 20vh;
    line-height: 20vh;
    display: grid;
    grid-template-columns: repeat(2, 2fr);
}
.item-nav {
    min-height: 120px;
}
/*Logo*/
.logo-container {
    text-align: start;
    padding-top: 1rem;
}
.icon-logo {
    font-size: 30px;
    background-color: var(--quarta-cor);
    padding: 0.5rem;
    border-radius: 50%;
}
/*NavBar*/
.navbar ul {
    display: flex;
    justify-content: space-around;
}
.navbar ul li {
    list-style-type: none;
}
.navbar ul li a {
    color: var(--segunda-cor);
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
}
.container-mobile {
    display: none;
    position: absolute;
    top: 0px;
    right: 30px;
    font-size: 25px;
    z-index: 2;
}
.btn-abrir-mobile {
    background-color: transparent;
    width: 50px;
    height: 50px;
    border: none;
    font-size: 25px;
}

@media(max-width:768px){
    .container-mobile {
        display: block;
    }
    .navegacao-desk {
        display: none;
    }
    .loading-open {
        display: block;
    }
}