:root {
  --primeira-cor: #fcd6d5;
  --segunda-cor: #122b9e;
  --terceira-cor: #fea8a8;
  --quarta-cor: #f9be46;
  --quinta-cor: #ffa8a8;
}
* {
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden;
  text-align: center;
  font-family: 'Raleway', sans-serif;
  background-image: url(./img/background.png);
  background-repeat: no-repeat;
}